/* 기본 스타일 */
@font-face {
  font-family: "MangoByeolbyeol";
  src: url("https://fastly.jsdelivr.net/gh/projectnoonnu/2405-3@1.1/MangoByeolbyeol.woff2")
    format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "yg-jalnan";
  src: url("https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_four@1.2/JalnanOTF00.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: yg-jalnan;
  margin: 0;
  padding: 0;
  background-color: #f8dcd4;
  text-align: center;
}

header {
  padding: 20px 0;
  background-color: #f8dcd4;
}

h1 {
  font-size: 4em;
  margin: 0;
  font-family: MangoByeolbyeol;
}

h2 {
  font-size: 2.5em;
  margin: 0;
  font-family: MangoByeolbyeol;
}

nav ul {
  list-style: none;
  padding: 0;
  margin: 0 20px 20px;
  display: flex;
  justify-content: center;
}

nav ul li {
  margin: 0 1.5em;
}

nav ul li a {
  text-decoration: none;
  color: #000;
  font-size: 1.7em;
}

.hero {
  margin: 20px auto;
}

.hero img {
  width: 300px;
  height: auto;
  border-radius: 10px;
}

.hero p {
  font-size: 1.2em;
  margin-top: 10px;
}

footer {
  margin-top: 20px;
}

.about-btn {
  background-color: #7f4bc9;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 20px;
  font-size: 1em;
  cursor: pointer;
}

.about-btn:hover {
  background-color: #6935a3;
}

/* 반응형 스타일 */

@media (max-width: 600px) {
  h1 {
    font-size: 2em;
  }

  h2 {
    font-size: 1.5em;
  }

  nav ul li a {
    font-size: 0.875em; /* 모바일에서 폰트 크기만 줄임 */
  }

  .hero img {
    width: 80%;
  }

  .hero p {
    font-size: 1em;
  }
}
